<template>
  <footer class="app-footer">
    <div>{{textDic.text0}}&copy;{{getYear()}} <a href="#">{{textDic.text1}}</a> {{textDic.text2}}</div>
  </footer>
</template>
<script>
export default {
  name: 'footer',
  methods: {
    getYear() {
      const date = new Date;
      return date.getFullYear(); 
    }
  },
  computed: {
    textDic() {
      return {
        text0: "Copyright",
        text1: "小房狗",
        text2: "版权所有"
      }
    }
  }
}
</script>



