import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
import Full from '@/containers/Full'
import Full2 from '@/containers/Full2'

import Buttons from '@/views/components/Buttons'

// Views - Pages
import Page404 from '@/views/errorPages/Page404'
import Page500 from '@/views/errorPages/Page500'
import Home from "@/views/Home"


/* login */
import Login from '@/views/login/index'
import Forgetpwd from '@/views/login/forgetpwd'
// const Login = _import('login/index');
Vue.use(Router);

export const constantRouterMap = [
    { path: '/login', component: Login, hidden: true },
    {path: '/login',redirect: '/login/index', name: 'Login',
          component: {
            render (c) { return c('router-view') }
              // Full,
          },
          children: [{path: 'login',  name: 'Login', component: _import('login/index') },
                     {path: 'forgetpwd',name: 'Forgetpwd',component: _import('login/forgetpwd')},
                    ]
    },
    {path: '/pages',redirect: '/pages/p404', name: 'Pages',
          component: {
            render (c) { return c('router-view') }
              // Full,
          },
          children: [{path: '404',  name: 'Page404', component: _import('errorPages/Page404') },
                     {path: '500',name: 'Page500',component: _import('errorPages/Page404')},
                    ]
    },
    { path: '/forgetpwd', component: Forgetpwd, hidden: true}


]

export default new Router({
  mode: 'hash', 
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});

export const asyncRouterMap = [

 {
    path: '/',
    redirect: '/Home',
    name: '管理平台',
    component: Full,
    hidden:false,
    children: [
      {path: '/Home',name: '首页',icon:'home',component: _import('Home')},
      {path: '/system',name: '系统管理',redirect: '/system/account',icon:'ios-alarm',
        component: {render (c) { return c('router-view') }},
        children: [ {path: 'account',name: '账号管理',icon:'university',component: _import('system/Account'), hidden:false, },
                    {path: 'charactar',name: '角色管理',icon:'arrow-graph-up-right',component: _import('system/Charactar')},
                    {path: 'setting',name: '设置',icon:'settings',component: _import('system/Setting')}
                  ]
      },
      {path: '/agent',name: '代理商信息',redirect: '/agent/agentManage',icon:'stats-bars',
        component: {render (c) { return c('router-view') }},
        children: [ {path: 'agentManage',name: '代理商信息',icon:'information',component: _import('agent/AgentManage'), hidden:false, },
                    
                  ]
      },
      {path: '/publish',name: '发布管理',redirect: '/publish/publishWebsite',icon:'android-map',
        component: {render (c) { return c('router-view') }},
        children: [ {path: 'publishWebsite',name: '发布站点管理',icon:'soup-can',component: _import('publish/PublishWebsite'), hidden:false, },
                  ]
      },
      {path: '/userManager',name: '用户信息管理',redirect: '/userManager/userInfo',icon:'man',
        component: {render (c) { return c('router-view') }},
        children: [ {path: 'userInfo',name: '用户信息',icon:'ios-paw',component: _import('userInfo/UserInfo') },
                    {path: 'suggestion',name: '意见反馈',icon:'arrow-graph-up-right',component: _import('userInfo/Suggestion')},
                  ]
      },
      {path: '/components',name: '组件',redirect: '/components/Alert',icon:'android-add',
        component: {render (c) { return c('router-view') }},
        children: [ {path: 'Alert',name: 'Alert',icon:'android-add-circle',component: _import('components/Alert'), hidden:false, },
                    {path: 'Buttons',name: 'Buttons',icon:'android-add-circle',component: _import('components/Buttons')},
                    {path: 'Card',name: 'Card',icon:'android-add-circle',component: _import('components/Card')},
                    {path: 'DatePicker',name: 'DatePicker',icon:'android-add-circle',component: _import('components/DatePicker')},
                    {path: 'Form',name: 'Form',icon:'android-add-circle',component: _import('components/Form')},
                    {path: 'HoverButtons',name: 'HoverButtons',icon:'android-add-circle',component: _import('components/HoverButtons')},
                    {path: 'Modal',name: 'Modal',icon:'android-add-circle',component: _import('components/Modal')},
                    {path: 'Select',name: 'Select',icon:'android-add-circle',component: _import('components/Select')},
                    {path: 'Spin',name: 'Spin',icon:'android-add-circle',component: _import('components/Spin')},
                    {path: 'Steps',name: 'Steps',icon:'android-add-circle',component: _import('components/Steps')},
                    {path: 'Timeline',name: 'Timeline',icon:'android-add-circle',component: _import('components/Timeline')},
                    {path: 'Timepicker',name: 'Timepicker',icon:'android-add-circle',component: _import('components/Timepicker')},
                    {path: 'Transfer',name: 'Transfer',icon:'android-add-circle',component: _import('components/Transfer')},
                    {path: 'Upload',name: 'Upload',icon:'android-add-circle',component: _import('components/Upload')},
                    {path: 'VueCalendar',name: 'VueCalendar',icon:'android-add-circle',component: _import('components/VueCalendar')},
                  ]
      },
    ]
  },

   {
    path: '/home1',
    redirect: '/home1/introduction',
    name: '首页2',
    component: Full2,
    hidden:false,
    children: [
     {path: '/home1/dashboard',name: 'Dashboard2',icon:'speedometer',component: _import('Dashboard2')},
     {path: '/home1/introduction',name: '介绍2',icon:'thumbsup',component: _import('Introduction')},
    
    ]
  },


  { path: '*', redirect: '/pages/404', hidden: true }
  
];



